import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  usersData: any = null;
  constructor(db: AngularFireDatabase) {
    db.list('/users').valueChanges().subscribe(
      data => {
        this.parseDataForTable(data);
        // this.usersData = data;
      },
      error => {
        console.log(error);
      }
    );

  }

  ngOnInit() {
  }

  parseDataForTable(data: any) {
    let users: any = [];
    data.forEach(user => {
      if (user.devices) {
        for (let device in user.devices) {
          const tempUser = {
            "userName": user.userName,
            "userRole": user.userRole,
            "battery": user.devices[device].battery,
            "deviceName": user.devices[device].deviceName,
            "location": user.devices[device].location,
            "status": user.devices[device].status,
            "version": user.devices[device].version,
            "deviceType": user.devices[device].deviceType
          };
          users.push(tempUser);
        }
      }
    });
    this.usersData = users;
  }
}
