import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'count'
})
export class CountPipe implements PipeTransform {

  transform(items: any[], key: string, value:string ): any {
    let count = 0;
    items.forEach(element => {
      if (element[key] === value) {
        count++;
      }
    });
    return count;
  }

}
